<template>
  <div id="app">
    <header style="position:fixed;width:100%;z-index:20;margin-top:0">
        <el-menu style="min-width:500px;" theme="dark" :default-active="activeIndex" class="el-menu-demo" mode="horizontal" @select="handleSelect">
          <el-menu-item style="font-size:20px;margin-left:50px;" index="1"><i class="el-icon-star-on "></i></el-menu-item>
          <el-menu-item style="font-size:20px;margin-left:50px;" index="2">Portfolio</el-menu-item>
          <el-menu-item style="font-size:20px;margin-left:50px;" index="3">Links</el-menu-item>
        </el-menu>
    </header>
    <div class="content">
      <router-view></router-view>
    </div>
    
  </div>
</template>

<script>
import Hub from "./Hub.js";
export default {
  data() {
      return {
        activeIndex: '1',
      };
    },
    methods: {
      handleSelect(key, keyPath) {
        // console.log(key);
        Hub.$emit('listenToChild', key);
      }
    }
}

</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  /* text-align: center;*/
}
.content{
  width: 70%;
  height: 100%;
  margin: 0 auto;
  background-color: #E5E9F2;
  min-width: 900px;
}
</style>



