<script>
import BScroll from "better-scroll";
import Hub from "../Hub.js";
export default {
  name: 'me',
  data () {
    return {
      domHeight:[],
      divList:[],
      scrollY:0,
      keyWord:'',
      pageScroll:'',
      imgdalog1:false,
      imgdalog2:false,
      imgdalog3:false,
    }
  },
  created(){
    
    this.$nextTick(()=>{
      this.pageinit();
      this.calculateHight();
      this.initScroll();
    })
    
  },
  computed:{
    curruntIndex(){
      for(let i=0;i<this.domHeight.length;i++){
        let height1 = this.domHeight[i];
        let height2 = this.domHeight[i+1];
        if(!height2 || (this.scrollY > height1 && this.scrollY < height2)){
          return i
        }
      }
      return 0
    }
  },
   watch:{
    keyWord(){
      console.log(this.keyWord,'1111111');
      // var el = this.divList[this.keyWord-1];
      // console.log(el,'111')
      // console.log(this.pageScroll)
      // this.pageScroll.scrollToElement(el,500);
      var el = this.domHeight[this.keyWord-1];
      console.log(el,'111')
      window.scrollTo(0,el);
      console.log('ok')
    }
  },
  methods:{
  pass(value){
    console.log('pass',value);
    this.keyWord = value;
  },
    pageinit(){
       Hub.$on('listenToChild',childData => {
        window.key = childData;
        this.pass(window.key)
        console.log(window.key)
       });
       
      let starBlock = document.getElementById("star-hook");
      let portfolioBlock = document.getElementById('portfolio-hook');
      let linksBlock = document.getElementById('links-hook'); 
      this.divList.push(starBlock);
      this.divList.push(portfolioBlock);
      this.divList.push(linksBlock);
      console.log(this.divList);
    },
    initScroll(){  
      this.pageScroll = new BScroll(this.$refs.pageWrapper,{probeType:3});
      this.pageScroll.on('scroll',(pos)=>{
        this.scrollY = Math.abs( Math.round(pos.y) );//取整取绝对值
        console.log(this.scrollY)
      })
    },
    calculateHight(){
      let starBlock = this.$refs.star;
      let portfolioBlock = this.$refs.portfolio;
      let linksBlock = this.$refs.links;
      let height = 0;
      this.domHeight.push(height);
      console.log(starBlock.clientHeight)
      height += starBlock.clientHeight - 60;
      this.domHeight.push(height);

      height += portfolioBlock.clientHeight ;
      console.log(portfolioBlock.clientHeight)
      this.domHeight.push(height);

      height += linksBlock.clientHeight;
      this.domHeight.push(height);
      console.log(this.domHeight)
    },
  }
}
</script>

